:root {
  --font-plex: "ibm-plex-mono", sans-serif;
}

:root {
  --black: rgb(30, 30, 30);
  --gray: rgb(232, 232, 232);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-plex);
  font-size: 0.75rem;
  font-style: normal;
  line-height: 1.2;
  color: var(--black);
  background: var(--gray);
}

.container {
  padding: 1rem;
}

.archive {
  display: flex;
  flex-wrap: wrap;
}

h1,
h2,
h3,
p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  color: var(--black);
}

.sidebar {
  flex-basis: 20rem;
  flex-grow: 1;
  flex-flow: column nowrap;
  height: 100vh;
  border-right: solid 1px var(--black);
}
@media (max-width: 39.9375em) {
  .sidebar {
    height: 170px;
    display: block;
    border-right: none;
    border-bottom: solid 1px var(--black);
  }
}
.sidebar__footer {
  border-top: solid 1px var(--black);
}
@media (max-width: 39.9375em) {
  .sidebar__footer {
    display: none;
  }
}
.sidebar #nsv-logo {
  fill: var(--black);
  width: 5rem;
  margin-bottom: 1rem;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 39.9375em) {
  .wrap {
    display: block;
  }
}

.body {
  flex-basis: 0;
  flex-grow: 999;
  min-width: 50%;
  height: 100vh;
  overflow: scroll;
}

.article {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 1rem;
}
@media (max-width: 63.9375em) {
  .article {
    display: block;
  }
}
@media (min-width: 64em) {
  .article {
    min-height: 475px;
  }
}
.article:not(:last-child) {
  border-bottom: solid 1px var(--black);
}
.article__info {
  flex-basis: 33.3333333333%;
}
@media (max-width: 39.9375em) {
  .article__info {
    padding-bottom: 1rem;
  }
}
.article__slideshow {
  flex-basis: 66.6666666667%;
  overflow: hidden;
}
.article .single-item {
  width: 100%;
}